import { ArrowRight } from "lucide-react"

export default function HowItWorks() {
  const steps = [
    { number: "01", title: "Download the App", description: "Download the ONE HEALTHLINE CONNECT app from the App Store or Google Play Store.", color: "bg-blue-600" },
    { number: "02", title: "Create an Account", description: "Sign up with your phone number or email and complete your health profile.", color: "bg-green-600" },
    { number: "03", title: "Choose a Service", description: "Select the healthcare service you need from our comprehensive offerings.", color: "bg-purple-600" },
    { number: "04", title: "Connect with Providers", description: "Get connected with healthcare providers, book appointments, or access emergency services.", color: "bg-teal-600" }
  ]

  return (
    <section className="py-20 bg-gray-900 text-white transition-colors duration-500">
      <div className="container mx-auto px-4">
        <div className="max-w-3xl mx-auto text-center mb-16">
          <h2 className="text-3xl font-bold mb-4 transition-colors duration-500">How It Works</h2>
          <p className="text-gray-300 transition-colors duration-500">
            Getting started with ONE HEALTHLINE CONNECT is easy. Follow these simple steps to access our healthcare services.
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
          {steps.map((step, index) => (
            <div key={index} className="relative transition-transform duration-500 hover:scale-105">
              {index < steps.length - 1 && (
                <div className="hidden lg:block absolute top-16 left-full w-full h-0.5 bg-gray-700 z-0">
                  <ArrowRight className="absolute right-0 top-1/2 transform -translate-y-1/2 text-gray-500 h-6 w-6" />
                </div>
              )}

              <div className="bg-gray-800 rounded-xl p-6 shadow-md relative z-10 h-full transition-colors duration-500">
                <div className={`${step.color} text-white text-2xl font-bold w-12 h-12 rounded-full flex items-center justify-center mb-6 transition-all duration-500`}>
                  {step.number}
                </div>
                <h3 className="text-xl font-bold mb-3 transition-colors duration-500">{step.title}</h3>
                <p className="text-gray-400 transition-colors duration-500">{step.description}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
